"use client";

import { useRouter } from "next/navigation";

const menuLinks: ReadonlyArray<MenuButton> = [
  { id: "admin", label: "Admin", path: "/editor" },
  { id: "cidade", label: "Cidade", path: "/city" },
  { id: "casa", label: "Casa", path: "/house" },
  { id: "farm", label: "Fazenda", path: "/farm" },
  { id: "forest", label: "Floresta", path: "/forest" },
  { id: "shops", label: "Lojas", path: "/shops" },
  { id: "chat", label: "Chat", path: "/chat" }
] as const;

type MenuButton = {
  id: string;
  label: string;
  path?: string;
  onClick?: () => void;
  icon?: string;
  ariaLabel?: string;
  active?: boolean;
};

type BottomMenuProps = {
  variant?: "page" | "overlay";
  buttons?: ReadonlyArray<MenuButton>;
  square?: boolean;
};

export function BottomMenu({ variant = "page", buttons, square = false }: BottomMenuProps = {}) {
  const router = useRouter();

  function handleNavigate(path: string) {
    if (typeof window !== "undefined") {
      window.localStorage.setItem("lastRoute", path);
      window.dispatchEvent(new CustomEvent("lastRouteChange", { detail: path }));
    }
    router.push(path);
  }

  const items = buttons ?? menuLinks;

  return (
    <nav className={`bottom-menu${variant === "overlay" ? " bottom-menu--overlay" : ""}`}>
      {items.map((item) => {
        const classes = [
          square ? "bottom-menu-square" : undefined,
          item.active ? "is-active" : undefined
        ]
          .filter(Boolean)
          .join(" ");
        return (
        <button
          key={item.id}
          type="button"
          className={classes || undefined}
          onClick={() => {
            if (item.onClick) {
              item.onClick();
            } else if (item.path) {
              handleNavigate(item.path);
            }
          }}
          aria-label={item.ariaLabel ?? item.label}
        >
          {item.icon ? <img src={item.icon} alt="" /> : item.label}
          {!item.icon && item.label}
        </button>
        );
      })}
    </nav>
  );
}
